*{
    padding: 0;
    margin: 0;
}
ul, ol { list-style:none; }
a{
  text-decoration: none;
}
.list{
  margin-top: 100px;
  margin-left:10px;
  height: 2000px;
}
.list li{
  display: block;
  border:3px solid white;
  position: relative;
  width:45%;
  height: 300px;
  margin-left: 5px;
  margin-top: 30px;
  float: left;
}
.span1{
  margin-bottom:10px;
  margin-left:3px;
  color:#333;
  display: block;
}
.span2{
  color:#ff5500;
  margin-left: 5px;
  height: 30px;
}
.span3{
  color:#999;
  margin-left:10px;
}
